.textGradientGold{
    background: radial-gradient(ellipse farthest-corner at right bottom, #FEDB37 0%, #FDB931 8%, #d9ae35 30%, #e2ab41 40%, transparent 80%),
    radial-gradient(ellipse farthest-corner at left top, #FFFFFF 0%, #FFFFAC 8%, #e8b647 25%, #e5b43f 62.5%, #dca030 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    /*-webkit-text-stroke: 0.5px black;*/
}

.textGradientSilver{
    background: linear-gradient(315deg, #a4a4a4 0%, #ebebeb 10%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    -webkit-text-stroke: 0.8px black;
}
